body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.TodoItem{
   font-size: 18px;
   color: rgb(92, 92, 92);
   padding:15px;
   border-radius: 5px;
   -webkit-transition: linear .3s;
   transition: linear .3s;
   display: flex;
   cursor: pointer;
   border-bottom: 1px solid rgb(235, 235, 235);
}
.TodoItem:hover{
   background: rgb(231, 231, 231);
}
.TodoItem-complete {
   color: #bebebe;
}
.TodoItem-complete p{
   text-decoration: line-through;
}
.title-complete{
   font-size: 15px;
   padding: 5px 5px 0 0;
   text-align: right;
   width: 100%;
   color: rgb(31, 201, 8);
   text-decoration: none !important;
}
p{
   margin-left: 20px;
   min-width: 290px;
}
.nf-checkedImg {
   -webkit-mask: url(/static/media/checkedImg.d294112f.svg) no-repeat;
   mask: url(/static/media/checkedImg.d294112f.svg) no-repeat;
   /*Your Size*/
   height: 25px;
   width: 25px;
   /*Your Color*/
   background: rgb(211, 211, 211);
   /*Your code css here*/
}
.nf-checkedImg-complete {
   -webkit-mask: url(/static/media/checkedImg.d294112f.svg) no-repeat;
   mask: url(/static/media/checkedImg.d294112f.svg) no-repeat;
   /*Your Size*/
   height: 25px;
   width: 25px;
   min-width: 25px;
   /*Your Color*/
   background: rgb(25, 141, 9);
   /*Your code css here*/
}
*{
   margin: 0;
   padding: 0;
}
.App{
   width: 100vw;
   height: 100vh;
}
.wrapper{
   width: 35%;
   background: white;
   border: 1px solid rgb(204, 204, 204);
   border-radius: 5px;
   box-shadow: 0px 0px 15px rgb(189, 189, 189);
   margin: 10% auto 0 auto;
}
.header{
   display: flex;
   padding: 20px 0 20px 15px;
   border-bottom: 1px solid rgb(197, 197, 197);
}
.header-img {
   -webkit-mask: url(/static/media/checkedImg.d294112f.svg) no-repeat;
   mask: url(/static/media/checkedImg.d294112f.svg) no-repeat;
   /*Your Size*/
   height: 28px;
   width: 28px;
   /*Your Color*/
   background: rgb(211, 211, 211);
   /*Your code css here*/
}
#input-text{
   width: 100%;
   margin-left: 25px;
   font-size: 18px;
   outline: none;
   color: rgb(85, 85, 85);
   border: none;
}
::-webkit-input-placeholder {
   /* Chrome/Opera/Safari */
   color: rgb(173, 173, 173);
   font-weight: 200;
}

::-moz-placeholder {
   /* Firefox 19+ */
   color: rgb(173, 173, 173);
}
